.progress {
	background-color: #d8d8d8;
	border-radius: 20px;
	position: relative;
	margin: 15px 0;
	height: 20px;
	width: 100%;
}

.progress-done {
	padding-left: 10px;
	background: linear-gradient(to left, #26c99b, #00A877);
	box-shadow: 0 3px 3px -5px #00A877, 0 2px 5px #00A877;
	border-radius: 20px;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 0;
	opacity: 0;
	transition: 1s ease 0.3s;
}
